.devDetails {
  padding-top: var(--bs-gutter-x, .75rem);

  .banner-img-container {
    height: 645px;

    > .row {
      max-height: 100%;
      height: 100%;

      > div {
        max-height: 100%;
        height: 100%;
        overflow: hidden;
      }
    }

    img {
      height: 100%;
      object-fit: cover;
      border-radius: 5px;
      min-width: 100%;
    }

    .btn-link-custom {
      left: 15px;
      top: 7px;
      //line-height: 1;
    }
  }

  .title-container {
    h1 {
      font-family: Rubik-Medium;
      font-size: 36px;
      color: #202330;
      line-height: 1;
      margin-bottom: 0;

    }

    .location {
      line-height: 1;
      margin-bottom: 14px;

      i {
        color: #029432;
      }
    }

    .price-container {
      font-family: Rubik-Medium;
      font-size: 26px;
      color: #029432;
      line-height: 20px;

      .price-label {
        color: #71757B;
      }

      .sqft {
        font-family: Rubik-Regular;
        font-size: 16px;
        color: #A0A4AA;
        //line-height: 20px;
      }
    }

  }

  .menuContainer {
    .detailsMenuSwiper {
      border-bottom: 1px solid #D9D9D9;
      padding-bottom: 7px;

      .swiper-slide {
        width: auto;
        font-family: Rubik-Regular;
        font-size: 14px;
        color: #000000;
        line-height: 20px;
        text-decoration: none !important;
        cursor: pointer;
      }

      .swiper-slide-active {
        color: #018E37;
      }
    }
  }

  .overview {
    .overview-items {
      .item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        flex: 1;
        text-align: center;
        margin-bottom: 25px;
        padding: 10px;

        @media (max-width: 767.98px) {
          flex-basis: 50%;
        }

        img {
          width: 40px;
          height: 40px;
          margin-bottom: 15px;

        }
      }
    }

    .offer-container {
      background: #F0F5FB;
      border-radius: 4px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .highlights {
    background: #202330;
    border-radius: 4px;
    padding: 30px 25px;
    margin-bottom: 35px;

    h2 {
      color: #FFFFFF;
    }

    .list-group {
      .list-group-item {
        background-color: transparent;
        border: none;
        font-family: Rubik-Regular;
        font-size: 16px;
        color: #CDD2D9;
        line-height: 30px;
        padding: 4px 0;

        i {
          color: #FFC107;
          margin-right: 7px;
          font-size: 19px
        }
      }
    }
  }

  .about-container {
    border-bottom: 1px solid #D9D9D9;

    p {
      font-family: Rubik-Regular;
      font-size: 15px;
      color: #71757B;
      line-height: 30px;

      div {
        display: none;
      }
    }

    .hide-btn-container {
      display: none !important;
    }

    .hide-text {
      p {
        overflow: hidden;
        text-overflow: unset;
        display: -webkit-box;
        -webkit-line-clamp: 6; /* number of lines to show */
        line-clamp: 6;
        -webkit-box-orient: vertical;
        margin-bottom: 0;
      }

      .hide-btn-container {
        display: flex !important;
        margin-top: -10px;
        background: rgb(255, 255, 255);
        background: linear-gradient(0deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.7679446778711485) 100%);
      }
    }
  }

  .features-container {
    position: relative;

    h3 {
      font-family: Rubik-Light;
      font-size: 22px;
      color: #202330;
    }

    p {
      font-family: Rubik-Regular;
      font-size: 15px;
      color: #71757B;
      line-height: 30px;
    }

    .hide-btn-container {
      display: none !important;
    }

    &.show-less {
      max-height: 500px;
      overflow: hidden;
      margin-bottom: 15px;

      .hide-btn-container {
        display: flex !important;
        margin-top: -10px;
        background: rgb(255, 255, 255);
        background: linear-gradient(0deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.5) 100%);
      }
    }
  }


  .floor-plan-container {
    background: #E4E8F6;
    border-radius: 4px;

    h2.title-content-1 {
      margin: 22px 15px 19px 20px;
      display: inline-block;
      line-height: 1;
    }

    .accordion {
      .accordion-button {
        background-color: #CFD4EB;
        outline: none;
        box-shadow: none;
        border: 0;
        padding: 10px 20px;
        font-family: Rubik-Medium;
        font-size: 15px;
        color: #5C668B;
      }

      .accordion-body {
        border-left: 1px solid #E4E8F6;
        border-right: 1px solid #E4E8F6;
        padding: 0;

        > .row {
          margin-left: 20px;
          margin-right: 20px;
        }

        .nav-pills {
          margin-bottom: -1px;

          .nav-link {
            font-size: 14px;
            text-align: center;
            color: #000000;
            border-radius: 0;
            margin-left: 15px;
            margin-right: 15px;
            padding: 15px 5px 10px 5px;
            font-family: Rubik-Regular;

            &.active {
              color: #018E37;
              background-color: transparent;
              border-bottom: 2px solid #018E37;
              font-family: Rubik-Medium;
            }

            &:first-child {
              margin-left: 0;
            }
          }
        }

        .btn-outline-danger {
          border-radius: 25px;
        }

        .tab-content {
          padding: 20px;

          .info-container {
            .info-item {
              display: flex;
              flex-direction: column;
              margin-bottom: 15px;

              > label {
                font-family: Rubik-Medium;
                font-size: 13px;
                color: #A0A5AD;
                line-height: 1;
                margin-bottom: 4px;
              }

              span {
                font-family: Rubik-Regular;
                font-size: 15px;
                color: #71757B;
                line-height: 1;
              }

              b {
                font-family: Rubik-Medium;
                font-size: 15px;
                color: #202330;
                line-height: 1;
              }

              .primary {
                color: #029432;
              }

              .custom-chip-checks {
                margin-top: 2px;

                label {
                  font-size: 13px;
                }

                .custom-chip-checks input[type=checkbox] + label, .custom-chip-checks input[type=radio] + label {
                  color: #4E5156;
                }

                .form-check {
                  pointer-events: none;
                }
              }

            }
          }

          .image-container {
            span {
              font-family: Rubik-Italic;
              font-size: 15px;
              color: #71757B;
              line-height: 30px;
              margin-top: 10px;
            }
          }
        }

      }
    }
  }

  .amenities-container {
    .items-container {
      display: flex;
      flex-wrap: wrap;

      .item {
        font-family: Rubik-Regular;
        font-size: 15px;
        color: #71757B;
        line-height: 30px;
        display: flex;
        width: 25%;
        margin-bottom: 20px;

        img {
          width: 25px;
          height: 25px;
          margin-right: 15px;
        }

      }
    }
    h3{
      color: #4C4F5F;
    }
    .additional-item{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      .item{
        background: #F4F4F4;
        border-radius: 3px;
        padding: 15px;
        line-height: 1;
        width: calc(33% - 4px);
        margin-bottom: 10px;

        font-family: Rubik-Regular;
        font-size: 15px;
        color: #71757B;

      }
    }
  }

  .specification-container{
    background: #DDF4F3;
    border-radius: 4px;
    padding: 25px 20px 15px 20px;

    .title{
      display: flex;
      align-items: center;
      margin-bottom: 10px;

      img{
        max-width: 35px;
        margin-right: 10px;
        max-height: 35px;
      }

      h3{
        font-family: Rubik-Light;
        font-size: 22px;
        color: #202330;
        margin-bottom: 0;
      }
    }

    .table-responsive{
      margin-left: -8px;

      table{
        tr{
          td,th{
            line-height: 1;
            font-family: Rubik-Regular;
            font-size: 14px;
            padding: 6px 8px;
          }
          td{
            color: #71757B;
          }

          th{
            color: #000000;
          }
        }
      }
    }

  }

  .videos-container{
    background: #202330;
    border-radius: 4px;
    padding: 20px;
    h2{
      color: #FFFFFF;
    }

    nav{
      border-bottom: 1px solid #4F5261;

      .nav{
        margin-bottom: -1px;

        .nav-link{
          font-family: Rubik-Regular;
          font-size: 15px;
          color: #FFFFFF;
          border-radius: 0;
          padding: 8px 10px;
        }
        .active{
          font-family: Rubik-Medium;
          color: #018E37;
          background-color: transparent;
          border-bottom: 2px solid #018E37;
        }
      }
    }

    .tab-content{
      video{
        //object-fit: cover;
        max-width: 100%;
      }
      .video-js{
        max-width: 100%;
        //object-fit: cover;

        button.vjs-big-play-button{
          border-radius: 50%;
          width: 49px;
          height: 48px;
        }

        .vjs-poster{
          //background-size: cover;
        }

      }
    }
  }

  .enquire-container{
    padding: 20px;
    background: #202330;
    border-radius: 4px;

    .info{
      span{
        font-family: Rubik-Regular;
        font-size: 15px;
        color: #A0A5AD;
        line-height: 30px;
      }
      .phone-info{
        font-family: Rubik-Regular;
        font-size: 22px;
        color: #FFFFFF;
        a{
          font-family: Rubik-Medium;
          font-size: 16px;
          color: #018E37;
          line-height: 20px;
        }
      }
    }

    form{
      font-family: Rubik-Regular;
      font-size: 15px;
      color: #71757B;
      line-height: 30px;

      .form-control,select{
        padding: 7px 12px;
      }
      textarea.form-control{
        padding: 11px 12px;
      }

      label{
        color: #A0A5AD;
      }
      a{
        color: #018E37;
      }
    }


  }

  .payment-plan-container{
    .swiper-wrapper{
      .swiper-slide{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0px 15px;
        position: relative;
        height: auto;

        &:after{
          content: '';
          position: absolute;
          right: 0;
          height: 50px;
          width: 1px;
          background-color: #D8D8D8;
        }

        span{
          font-family: Rubik-Light;
          font-size: 22px;
          color: #018E37;
          text-align: center;
        }
        p{
          font-family: Rubik-Regular;
          font-size: 15px;
          color: #000000;
          text-align: center;
          line-height: 20px;
          margin-bottom: 0;
        }
      }
    }

    .custom-swiper-nav{
      margin-top: -10.5 !important;
      border: 1px solid #f77a27 !important;
    }

    .swiper-button-next{
      right: -15px;
    }
    .swiper-button-prev{
      margin-left: -23px;
    }

  }

  .bank-loan-container{
    @extend .payment-plan-container;

    .swiper-slide{
      background: #FFFFFF;
      border: 1px solid #E6E6E6;
      border-radius: 3px;
      padding: 20px 15px !important;
      box-shadow: 0px 2px 6px 0px rgb(210, 210, 210);

      &:after{
        display: none;
      }
    }

    img{
      max-width: 100%;
      max-height: 75px;
    }
    label{
      font-family: Rubik-Medium;
      font-size: 13px;
      color: #A0A5AD;
      text-align: center;
      line-height: 20px;
      margin-bottom: 3px;
    }
    .value{
      font-family: Rubik-Medium;
      font-size: 17px;
      color: #202330;
      text-align: center;
      line-height: 25px;
    }
  }

  .chat-container{
    background-image: url("./assets/img/details/chat-bg.png");
    padding: 35px 20px;
    display: flex;
    align-items: center;
    a{
      font-family: Rubik-Medium;
      font-size: 16px;
      color: #018E37;
      line-height: 20px;
    }
  }

  .map-view{
    .swiper-slide{
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      font-family: Rubik-Regular;
      font-size: 14px;
      color: #018E37;
      text-align: center;
      line-height: 20px;
      width: auto;
      div{
        border: 1px solid #D8D8D8;
        border-radius: 5px;
        padding: 12px 30px;
        margin-bottom: 7px;
        width: 100px;
        height: 60px;

        img{
          width: 30px;
          height: 30px;
        }
      }
    }

    .custom-swiper-nav{
      border: 1px solid #f77a27;
    }

    .mapView-prev{
      margin-left: -23px;
    }
    .mapView-next{
      margin-right: -15px;
    }

    .googlemap {
      position: relative;
      padding-bottom: 45%; /* 4:3 ratio*/
      height: 0;
      overflow: hidden;
    }

    .googlemap iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100% !important;
      height: 100% !important;
    }
  }

  .locality-container{
    @extend .specification-container;

    background: #F4F4F4;
    border-radius: 4px;

    .item{
      margin-bottom: 25px;
    }

    .list-group{
      .list-group-item{
        font-family: Rubik-Regular;
        font-size: 16px;
        color: #71757B;
        background-color: transparent;
        border: 0;
        display: flex;
        align-items: center;
        line-height: 1;
        padding: 6px 15px;

        i{
          color: #71757B;
          margin-right: 7px;
          font-size: 23px;
        }
      }
    }
  }


  .contact-form {
    background: #F6F6F6;
    border: 1px solid #E1E1E1;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15);
    border-radius: 3px;
    padding: 15px;

    .title-box {
      @extend .title-container;

    }
  }
}
